.codeEditorGrid {
  display: grid;
  grid-template-areas: 'c c c c e e';
  grid-column-gap: 1rem;
}

.editorColumn {
  grid-area: c;
  padding-top: 0.5rem;
}

.editorColumn .editorHeader {
  font-weight: bold;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.editorWrapper {
  height: 400px;
  overflow: scroll;
}

.editor {
  font-family: 'jetbrains-mono', monospace;
  counter-reset: line;
  border: 1px solid #ced4da;
  font-size: 12px;
  line-height: 20px;
  vertical-align: top;
  min-height: 100%;
}

.codeArea {
  outline: none;
  padding-left: 60px !important;
}

.editor pre {
  /*padding-left: 60px !important;*/
  height: 100vh;
}

.examplesColumn {
  grid-area: e;
}


@media (max-width: 900px) {
  .codeEditorGrid {
    grid-template-areas: 'c c' 'e e';
  }
}